define("nbody/timer", ["exports"], function (exports) {
  "use strict";

  exports["default"] = timerFactory;
  var SECOND = 1000;
  var MINUTE = SECOND * 60;
  var HOUR = MINUTE * 60;
  var DAY = HOUR * 24;
  var MONTH = DAY * 31;
  var YEAR = DAY * 365;

  var Timer = {
    units: Object.freeze({
      SECOND: SECOND,
      MINUTE: MINUTE,
      HOUR: HOUR,
      DAY: DAY,
      MONTH: MONTH,
      YEAR: YEAR
    }),

    time: 0,
    dt: 5 * MINUTE,

    advance: function advance(increment) {
      this.time += increment || this.dt;
    }
  };

  function timerFactory() {
    return Object.create(Timer);
  }
});